.header{
    position: fixed;
    width: 100%; 
    top: 0;
    left: 0;
    z-index: 5;
    max-height: 80px;

.header__content{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 13px 0;
.header__main{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
}
    .btn_black{
        display: inline-block;
        position: relative;
        width: 100%;
        color: $white;
        font-size: 14px;
        font-weight: 500;
        line-height: 17.6px;
        letter-spacing: -0.14px;
        text-transform: uppercase;
        padding: 18px 32px;
        border: 1px solid $white;
        transition: $transition_all_03;
        
        &:hover {
            background-color: $white;
            transition: $transition_all_03;
            color: $black;
        }

}
.burger-btn{
    background-color: rgba(28,28,28,0);
    display: flex;
    align-items: center;
    position: relative;
    width: 100%;
    color: $white;
    font-size: 14px;
    font-weight: 500;
    line-height: 17.6px;
    letter-spacing: -0.14px;
    text-transform: uppercase;
    padding: 15px;
    border: 1px solid $white;
    transition: $transition_all_03;
    .text_burger_btn {
       margin-left: 10px;

        &::before{
    content: 'menu';
    }
    }
}
.burger {
    position: relative;
    width: 24px;
    height: 24px;
    z-index: 5;
span {
    position: absolute;
    top: 11px;
    left: 0;
    width: 100%;
    height: 2px;
    background-color: $white;
    border-radius: 10px;
    transition: all .7s ease 0s;
    
}
&::before, &::after {
    content: '';
    position: absolute;
    right: 0;
    height: 2px;
    width: 100%;
    background-color: $white;
    border-radius: 10px;
}
&::before {
    top: 4px;
}

&::after {
    bottom: 4px;
}


}
.burger_active {

    span {
        opacity: 0;
    }

    &::before,
    &::after {
        animation-duration: .7s;
        animation-fill-mode: forwards;
    }

    &::before {
        animation-name: burger1;
    }

    &::after {
        animation-name: burger2;
    }

@keyframes burger1 {
    0% {
        top: 0px;
    }

    40% {
        transform: rotate(0deg);
        top: 11px;
    }

    100% {
        transform: rotate(45deg);
        top: 11px;
    }
}

@keyframes burger2 {
    0% {
        bottom: 0;
    }

    40% {
        transform: rotate(0deg);
        bottom: 11px;
    }

    100% {
        transform: rotate(-45deg);
        bottom: 11px;
    }
}
}
.burger_finish {

    &::before,
    &::after {
        animation-duration: .7s;
        animation-fill-mode: forwards;
    }

    &::before {
        animation-name: burger3;
    }

    &::after {
        animation-name: burger4;
    }

    @keyframes burger3 {
        0% {
            transform: rotate(45deg);
            top: 6px;
        }

        40% {
            transform: rotate(0deg);
            top: 11px;
        }

        100% {
            top: 4px;
        }
    }

    @keyframes burger4 {
        0% {
            transform: rotate(-45deg);
            bottom: 11px;
        }

        40% {
            transform: rotate(0deg);
            bottom: 6px;
        }

        100% {
            bottom: 4px;
        }
    }
}

.text_burger_btn-click {
  
     &::before{
        animation-name: text-btn-burger;
        animation-duration: .7s;
            animation-fill-mode: forwards;
        @keyframes text-btn-burger {
            0% {
           content: 'menu';
            }
    
            100% {
                content: 'Close';
            }
 }
 }
}
.menu{
    position: relative;
    z-index: -1;
    position: fixed;
    min-width: 609px;
    height: 100%;
    overflow: auto;
    top: 0;
    left: -100%;
    background-color: $black;
    padding: 80px 0px 20px 20px;
    transition: all .5s ease 0s;
    display: flex;
    flex-direction: column;

    @include media ('<tablet'){
        min-width: 100%;
        padding: 210px 20px 80px 20px;
        text-align: center;
    }

    @include media ('<phone'){
        padding: 150px 20px 80px 20px;
    }

}
.menu_active {
    left: 0;
}
.menu__wrap{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
    z-index: 1;
    border: 1px solid $white;
    padding: 40px;
    .site_btn_white {
        @include adaptiv-vaule("font-size", 20, 14, 1);
    }
}


.menu__list{
    padding: 0;
    margin: 0;
    list-style: none;
}

.menu__item{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 35px;
    position: relative;
    padding-right: 58px;

    @include media ('<tablet'){
        justify-content: center;
    }
    &::after{
        content: '';
        position: absolute;
        right: 0;
        background: url(../images/arrrow-menu.svg) no-repeat;
        width: 32px;
        height: 32px;
        opacity: 0;
        transition: 1s;
       
    }
    &:hover::after{
        opacity: 1;
    }
  
    a{
        transition: color .3s ease 0s;
        @include adaptiv-vaule("font-size", 42, 26, 1);
        color: $white;
        position: relative;
        margin-bottom: 5px;
        opacity: 60%;
        font-weight: 500;
        @include adaptiv-vaule("line-height", 42, 26, 1);
        letter-spacing: -3%;;
        text-transform: uppercase;
        transition: 1s;
    

        &:hover{
          opacity: 1;
        }

    }
}

}
}